@import url(http://fonts.googleapis.com/earlyaccess/notosanskr.css);
*{margin:0;padding:0;border:0;font-size:100%;vertical-align:baseline;background:transparent;border-collapse:collapse;box-sizing:border-box;-webkit-text-size-adjust:none}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{width:100%;height:100%;font-family:'Noto Sans KR', sans-serif;color:#1a1a1a;line-height:1.5;font-size:14px;overflow-x:hidden;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}table{border-collapse:collapse;border-spacing:0}button{background-color:transparent;outline:none;border:0;cursor:pointer;font-family:inherit}dd{-webkit-margin-start:0}ul{-webkit-padding-start:0;list-style-position:inside}ol,ul,li{list-style:none}a{margin:0;padding:0;font-size:100%;color:inherit;text-decoration:none;background:transparent}img{max-width:100%;height:auto;vertical-align:top}b,span{font-size:inherit;color:inherit}br{line-height:inherit}

.App{text-align:center}.App .App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App .App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App .App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App .App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

